import React, { Component } from 'react'
import axios from 'axios'
const ApiUrl = "https://restaurentappapi.herokuapp.com/orders"
const updateUrl = "https://restaurentappapi.herokuapp.com/updateorderstatus"

class Orders extends Component {

    constructor() {
        super()
        this.state = {
            orders: '',
            click: 1
        }
    }
    renderOrders = (data) => {
        if (data) {
            return data.map((order) => {
                return (
                    <tr key={order.bookingId}>
                        <td>{order.bookingId}</td>
                        <td>{order.name}</td>
                        <td>{order.restaurentname}</td>
                        <td>{order.dish}</td>
                        <td>{order.item}</td>
                        <td>{order.phonenumber}</td>
                        <td>{order.address}</td>
                        <td>
                            {
                                order.status ? <p>accepted</p> :
                                    <input type="button" value="accept" onClick={() => {
                                        axios.put(updateUrl, {
                                            "bookingId": order.bookingId,
                                            "status": true
                                        })
                                    }} />
                            }
                        </td>
                    </tr>
                )
            })
        }
        else {
            return (
                <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td> <img src="img/loading.gif" alt="loading" height="400px" /></td>
                </tr>
            )
        }
    }
    render() {
        axios.get(ApiUrl)
            .then((res) => {

                this.setState({ orders: res.data })
            })
        return (
            <div>
                <table className='table'>
                    <thead>
                        <tr>
                            <th>Order Id</th>
                            <th>Name</th>
                            <th>Restaurent Name</th>
                            <th>Dish Name</th>
                            <th>Quantity</th>
                            <th>Phone No</th>
                            <th>Address</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.renderOrders(this.state.orders)
                        }
                    </tbody>
                </table>
                <div id="loading"></div>
            </div>
        )
    }


}

export default Orders
