import React, { useEffect, useState } from 'react'
import axios from 'axios'
const ApiUrl = "https://restaurentappapi.herokuapp.com/mealtype"
function Mealtype() {
    const [meal, setmeal] = useState([])
    useEffect(() => {
        axios.get(ApiUrl)
            .then((res) => {
                setmeal(res.data)
            })
    }, [])
    return (
        <table className="table">
            <thead>
                <tr>
                    <th>Meal Id</th>
                    <th>Meal Name</th>
                </tr>
            </thead>
            <tbody>
                {
                    meal.map((data) => {
                        return (
                            <tr key={data._id}>
                                <td>{data.mealtype}</td>
                                <td>{data.name}</td>
                                <td></td>
                            </tr>
                        )
                    })
                }
            </tbody>
        </table>
    )
}

export default Mealtype
