import React, { useEffect, useState } from 'react'
import axios from 'axios'
const ApiUrl = "https://restaurentappapi.herokuapp.com/cuisine"

function Cuisine() {
    const [cuisine, setcuisine] = useState([])
    useEffect(() => {
        axios.get(ApiUrl)
            .then((res) => {
                setcuisine(res.data)
            })
    }, [])
    return (
        <div>
            <table className="table">
                <thead>
                    <tr>
                        <th>Cuisine Id</th>
                        <th>Cuisine Name</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        cuisine.map((data) => {
                            return (
                                <tr key={data._id}>
                                    <td>{data.cuisine}</td>
                                    <td>{data.name}</td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>
        </div>
    )
}

export default Cuisine
