import React, { useState, useEffect } from 'react'
import axios from 'axios'
const ApiUrl = "https://restaurentappapi.herokuapp.com/restaurent"

function Restaurent() {
    const [rest, setrestaurent] = useState([])
    useEffect(() => {
        axios.get(ApiUrl)
            .then((res) => {
                setrestaurent(res.data)
            })
    }, [])
    return (
        <div className="restaurent">
            <table className="table">
                <thead>
                    <tr>
                        <th>Sr</th>
                        <th>Restaurent Name</th>
                        <th>Address</th>
                        <th>City</th>
                        <th>Contact</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        rest.map((data) => {
                            return (
                                <tr key={data._id}>
                                    <td>{data._id}</td>
                                    <td>{data.name}</td>
                                    <td>{data.address}</td>
                                    <td>{data.city_name}</td>
                                    <td>{data.contact_number}</td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>
        </div>
    )
}

export default Restaurent
