import React from 'react'
import { Link } from 'react-router-dom'
import '../css/Sidebar.css'
const SideBar = () => {
    return (
        <div className="sidebar">
            <Link to='/' style={{ textDecoration: 'none', color: 'white' }}>
                <div className="brand-name">
                    <img src='/img/shop.png' />
                    <p>Edumeto</p>
                </div>
            </Link>
            <div className="sidebar-headings">
                <Link to={'/orders'} className="link" style={{ textDecoration: 'none', color: 'white' }}>
                    <div className="flex-box">
                        <img src="/img/order.png" />
                        <p>Orders</p>
                    </div>
                </Link>
                <Link to={'/restaurent'} className="link" style={{ textDecoration: 'none', color: 'white' }}>
                    <div className="flex-box">
                        <img src='/img/restaurent.png' />
                        <p>Restaurents</p>
                    </div>
                </Link>
                <Link to={'/city'} className="link" style={{ textDecoration: 'none', color: 'white' }}>
                    <div className="flex-box">
                        <img src='/img/city.png' />
                        <p>City</p>
                    </div>
                </Link>
                <Link to={'/cuisine'} className="link" style={{ textDecoration: 'none', color: 'white' }} >
                    <div className="flex-box">
                        <img src="img/cuisine.png" />
                        <p>Cuisne Types</p>
                    </div>
                </Link>
                <Link to={'/mealtype'} className="link" style={{ textDecoration: 'none', color: 'white' }}>
                    <div className="flex-box">
                        <img src="/img/meal.png" />
                        <p>Meal Types</p>
                    </div>
                </Link>

            </div>
        </div>
    )
}
export default SideBar