import React, { useEffect, useState } from 'react'
import axios from 'axios'
const Apiurl = "https://restaurentappapi.herokuapp.com/city"
function City() {
    const [city, setCity] = useState([])
    useEffect(() => {
        axios.get(Apiurl)
            .then((res) => {
                setCity(res.data)
            })
    }, [])
    return (
        <>
            <table className="table">
                <thead>
                    <tr>
                        <th>City Id</th>
                        <th>City Name</th>
                        <th>Country</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        city.map((data) => {
                            return (
                                <tr key={data._id}>
                                    <td>{data.city}</td>
                                    <td>{data.city_name}</td>
                                    <td>{data.country_name}</td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>
        </>
    )
}

export default City
